import React, { Component } from 'react';

export default class FooterBanner extends Component {

      constructor(props) {
            super(props);

      }
      render() {

            let currentUrl = window.location.pathname
            let imageUrl = "/brutal-fruit/images/home-footer.png";
            let footerText = "you belong to fit in";
            
            if(currentUrl ="/store-locator") {
                  imageUrl = "/brutal-fruit/images/footer-store.png";
                  footerText = "Get invites to our exclusive launches";
            }


            
            return (

                  <div className="footer-banner" style={{backgroundImage: "url(" + imageUrl + ")"}}>
	                  <h1 className="text-center text-uppercase">{footerText}</h1>
                        <p className="text-center"><button className="btl-btn btl-btn-secondary">Join The Suite</button></p>
                  </div>

            
            );
      }
}
